<template>
   <li :class="{'completed': item.isDone}">
              <div class="view">
                  <input class="toggle" type="checkbox" @change="ipt(item.id)" :checked="item.isDone">
                  <label>{{item.name}}</label>
                  <button class="destroy" @click="del(item.id)"></button>
              </div>
          </li>
</template>

<script>

export default {
    props:["item"],
    methods:{
        del(id){
            this.$emit("name",id)
        },
        ipt(id){
           this.$emit("ipt",id) 
        }
    }
}
</script>

