<template>
  <section class="todoapp">
    <!-- 头部：输入框 -->
    <header class="header">
      <h1>todos</h1>
      <input  v-model="name" @keydown.enter="add" class="new-todo" placeholder="输入新计划" autofocus />
    </header>

    <!-- 列表： -->
    <section class="main">
      <input v-model="qx" id="toggle-all" class="toggle-all" type="checkbox" />
      <label  for="toggle-all">Mark all as complete</label>
      <ul class="todo-list">

        <todo v-for="item in show" :key="item.id" :item="item" @name="del" @ipt="ipt"></todo>

      </ul>
    </section>

    <!-- 底部：状态栏 -->
    <footer class="footer">
      <span class="todo-count">剩余<strong>{{sheng}}</strong>未完成 </span>
      <ul class="filters">
        <li>
          <a :class="{'selected':a=='all'}" @click="fn('all')" href="#/">全部</a>
        </li>
        <li>
          <a :class="{'selected':a=='sll'}" @click="fn('sll')" href="#/active">未完成</a>
        </li>
        <li>
          <a :class="{'selected':a=='ell'}" @click="fn('ell')" href="#/completed">已完成</a>
        </li>
      </ul>
      <button class="clear-completed" @click="clear">清除已完成</button>
    </footer>
  </section>
</template>
<script>
import todo from "./components/todo.vue";
export default {
  components: {
    todo,
  },
  data() {
      return {
       list:JSON.parse(localStorage.getItem('todoList'))||[],
       show:JSON.parse(localStorage.getItem('todoList'))||[],
        name:'',
        a:'all'
      };
    },
    methods:{
      del(id){
        this.list = this.list.filter(ele=>ele.id !== id)
        this.show=this.list
              },
      add(){
        if(this.name!=''){
        this.list.unshift({
          id:Math.random(),
          name:this.name,
          isDone:false
        })
        this.show=this.list
        this.name=""
        }
      },
      ipt(id){
        this.list.find(ele=>ele.id == id).isDone =! this.list.find(ele=>ele.id == id).isDone
      },
      clear(){
        this.list= this.list.filter(ele=>ele.isDone == false)
        this.show=this.list
      },
      fn(val){
        this.a= val
        if(this.a=='all'){
          this.show=this.list
        }else if(this.a=='sll'){
          this.show=this.list.filter(ele=>ele.isDone==false)
        }else{
          this.show=this.list.filter(ele=>ele.isDone==true)
        }
      }
    },
    computed:{
      sheng(){
        return this.list.filter(ele=>ele.isDone == false).length
      },
      qx:{
        get(){
         return this.list.every(ele=>ele.isDone)
        },
        set(val){
          this.list.forEach(ele=>ele.isDone = val)
        }
      }
    },
    watch:{
      list:{
      deep:true,
      handler(val){
        localStorage.setItem("todoList",JSON.stringify(val))
      }
      }
    }
};
</script>

